:root {
  --suisse: suisse;
  --gilroy: gilroy;
  --white: #ffffff;
  --black: #464646;
  --grey: #DEDEDE;
  --brown: #72584F;
  --light-brown: #F5F2EF;
  --gold: #D8B86F;
  --gold-dark: #9F823C;
  --gold-gradient: linear-gradient(94.18deg, #9F823C 0%, #D8B86F 62.68%);
  --green: #40664C;
  --green-gradient: linear-gradient(267.47deg, #40664C 0.98%, #709471 75.13%);
  --radius: 15px;
  --transition: 0.3s ease all;
  --primary-color: #131313;
}

.cta {
  max-width: 200px;
}

.main {
  background-image: url(../img/jpg/bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 200px;
}
.header {
  color: var(--white);
  padding: 30px 0px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__wrapper {
  flex: 0 0 40%;
}
.header__burger {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: flex-end;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 4%;
  text-transform: uppercase;
  line-height: 1.4;
}
.header__switcher {
  flex: 0 0 20%;
  text-align: right;
  padding-right: 100px;
  font-weight: 500;
}
.header__choose {
  flex: 0 0 20%;
}

[class*=__container] {
  max-width: 1680px;
  margin: 0 auto;
  padding: 0px 15px;
  min-width: 0;
}

body {
  color: var(--black);
  font-family: var(--suisse);
  font-weight: 400;
}